<template>
    <div class="m-joke-item">
        <div class="content">
            <i class="u-star" v-if="joke.mark && joke.mark.length">★</i>
            <span class="u-sentence" v-html="parse(joke.post_title)"></span>
        </div>
        <div class="misc">
            <div class="op">
                <el-link
                    type="primary"
                    class="copy-btn"
                    :disabled="disabled"
                    @click="handleCopy(joke.post_title)"
                >
                    <i class="el-icon-document-copy"></i>
                    {{ copyLabel }}
                </el-link>
                <router-link
                    v-if="mode != 'single'"
                    class="el-link el-link--primary is-underline"
                    :to="'/joke/' + joke.ID"
                >
                    <i class="el-icon-chat-dot-square"></i>评论
                </router-link>
                <a v-if="mode == 'single'" class="u-edit el-link el-link--primary is-underline" :href="editLink('joke',joke.ID)" target="_blank"><i class="el-icon-edit-outline"></i> 编辑</a>
            </div>
            <div class="other">
                <div class="user">
                    <img width="24" height="24" :src="(joke.author_info && joke.author_info.user_avatar) | showAvatar" />
                    <a
                        :href="joke.post_author | authorLink"
                        target="_blank"
                        v-if="joke.post_author && joke.post_author!=1"
                    >{{ joke.author }}</a>
                    <span v-else>{{ joke.author || '匿名' }}</span>
                </div>
                <div class="time">
                    <span class="u-date">
                        <i class="el-icon-date"></i>&emsp;
                        <time>{{ joke.post_date | dateFormat }}</time>
                    </span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import JX3_EMOTION from "@jx3box/jx3box-emotion";
import dateFormat from "@/utils/dateFormat";
import { showAvatar, authorLink,editLink } from "@jx3box/jx3box-common/js/utils";
export default {
    name: "joke_item",
    props: ["joke", "mode"],
    data() {
        return {
            copyLabel: "点击复制",
            disabled: false,
        };
    },
    filters: {
        dateFormat: function (val) {
            return dateFormat(new Date(val));
        },
        showAvatar: function (val) {
            return showAvatar(val, 24);
        },
        authorLink,
    },
    methods: {
        parse(str) {
            const ins = new JX3_EMOTION(str);
            return ins.code;
        },
        handleCopy(str) {
            this.disabled = true;
            navigator.clipboard.writeText(str).then(() => {
                this.copyLabel = "已复制";

                setTimeout(() => {
                    this.copyLabel = "点击复制";
                    this.disabled = false;
                }, 3000);
            });
        },
        editLink
    },
};
</script>
